import '../css/ultradent-elite.scss';

import 'jquery.tabs';
import 'jquery.quickview';
import Module from './modules/core/Module';

$( document ).ready( function () {

    $( '#page-content' ).tabs( {
        enable_hash: true
    } );

    $( '.tooltip-content-wrapper' ).QuickView( {trigger_class: 'udIcon tooltip'} );

    var ue_dashboard;
    if ( document.getElementById( 'fiscalPurchaseTotal' ) ) {
        ue_dashboard = (new UpiEliteDashboard).initialize( '#upiEliteDashboard:not(.disabled)' );
    }

} );

var UpiEliteDashboard = function () {

    var conf = {
        programs: {
            1: {name: 'silver', threshold: 3000},
            2: {name: 'gold', threshold: 6000},
            3: {name: 'platinum', threshold: 12000}
        }
    };

    var fiscal_purchases = 0;

    return $.extend( Module, {

        elements: {
            '#eliteIncemtiveMeter': 'meter',
            '#meterCounter': 'meter_counter',
            '#meterValue': 'meter_value',
            '#fiscalPurchaseTotal': 'fiscal_total'
        },

        initialize: function ( el ) {
            this.el = $( el );
            this.update_elements();
            this.delegate_events();

            fiscal_purchases = parseFloat( this.fiscal_total[0].getAttribute( 'data-fiscal-purchase-total' ) || 0 );

            // fiscal_purchases NaN return, unable to perform calcualtions
            if ( isNaN( parseFloat( fiscal_purchases ) ) ) {
                return;
            }

            this.meter_counter.show();
            setTimeout( this.proxy( function () {
                var pos = this.calculate_meter_pos();
                this.set_meter_pos( pos );
            } ), 1000 );
        },

        get_threshold: function ( id ) {
            return (conf.programs[id]) ? conf.programs[id].threshold : 0;
        },

        get_current_tier: function () {
            this._current_tier = this._current_tier = get_tier();

            function get_tier () {
                var programs = conf.programs, tier = 0;
                for ( var key in programs ) {
                    if ( programs.hasOwnProperty( key ) ) {
                        if ( fiscal_purchases >= programs[key].threshold ) {
                            tier = key;
                        }
                    }
                }
                return parseInt( tier, 10 );
            };

            return this._current_tier;
        },

        get_next_tier: function ( i ) {
            var max = 0, p = conf.programs;
            // get max number of tiers
            for ( var key in p ) {
                if ( p.hasOwnProperty( key ) ) {
                    max++;
                }
            }

            return (i + 1 < max) ? i + 1 : max;
        },

        get_indicator_pos_for: function ( id ) {
            // find position of indicator for given program, null returns meter_counter
            var pos = {x: 0, y: 0},
                obj = this.el.find( (id
                    ? '.' + conf.programs[id].name
                    : this.meter_counter.selector) + ' .indicator' )[0];

            if ( obj && obj.offsetParent ) {
                do {
                    pos.x += obj.offsetLeft;
                    pos.y += obj.offsetTop;
                    obj = obj.offsetParent;
                }
                while ( obj !== this.meter[0] );
            }
            return pos;
        },

        calculate_meter_pos: function () {
            // get current incentive tier
            var c = this.get_current_tier(),
                n = this.get_next_tier( c ),
                // get pos of next incentive
                cpos = this.get_indicator_pos_for( c ),
                npos = this.get_indicator_pos_for( n ),
                // calculate diff between current and next tiers
                t1 = this.get_threshold( c ),
                t2 = this.get_threshold( n );

            // prevent division by zero: (t2-t1) || 1
            return ((npos.x - cpos.x) * ((fiscal_purchases - t1) / (t2 - t1 || 1))) + cpos.x;
        },

        set_meter_pos: function ( pos ) {
            this.meter_counter.animate( {
                    left: parseInt( pos, 10 ) || 0
                },
                {
                    easing: 'easeOutCirc',
                    duration: 1500,
                    step: this.proxy( function ( now, fx ) {
                        this.set_meter_value( parseFloat( (now / pos) * fiscal_purchases ) );
                    } ),
                    complete: this.proxy( function () {
                        var curr = this.get_current_tier();
                        this.set_meter_value( fiscal_purchases );
                        if ( curr > 0 ) {
                            this.focus_tier( curr );
                        }
                    } )
                } );
        },

        set_meter_value: function ( val ) {
            this.meter_value.text( (!isNaN( val )) ? parseFloat( val ).toCurrency() : 0 );
        },

        focus_tier: function ( id ) {
            this.el.find( '.' + conf.programs[id].name + ' img' ).animate( {
                    width: 150,
                    height: 150,
                    top: -25
                },
                {
                    duration: 600,
                    easing: 'easeOutBounce'
                } );
        }

    } );
};



