/**
 * Toggle a hidden from a dynamically generated trigger element
 */
$.fn.QuickView = function(opt) {
    var defaults = {
            effect_speed: 150,
            delay: 300,
            trigger_template: '<span></span>',
            trigger_class: 'link1',
            label: 'Quick View',
            auto_hide: true,
            remove_trigger: false,
            position: '' // space separate string 'bottom right'
        },
        opt = $.extend(defaults, opt);

    return this.each(function() {
        var elm = $(this),
            close_button = $('<span class="close"></span>'),
            positions = opt.position.split(' '),
            elm_trigger = $(opt.trigger_template)
            .addClass(opt.trigger_class)
            .html(elm.attr('data-label') || opt.label);

        if (positions.length && '' !== positions[0]) {
            var positionClasses = [];
            $.each(positions, function(i, pos) {
                positionClasses.push('pos-' + pos);
            })
            elm.addClass(positionClasses.join(' '));
        }
        // ad trigger to DOM, append close button to quickview elm
        elm_trigger.insertBefore(elm.prepend(close_button).hide());

        // bind events
        elm_trigger
            .click(function() {
                elm.trigger('show');
            });

        close_button.on('click', function(e) {
            elm.trigger('hide');
        });

        // bind to custom events
        elm.on('show', show);
        elm.on('hide', hide);

        if (opt.remove_trigger) {
            elm.on('opened', function() {
                elm_trigger.remove();
            });
        }

        function show(e) {
            elm.addClass('fadeUp').show().trigger('opened');
        };

        function hide(e) {
            elm.removeClass('fadeUp').hide().trigger('closed');
        };

    });
};



